html {
  box-sizing: border-box; }

*,
*:after,
*:before {
  box-sizing: inherit; }

body {
  background: #fefefe;
  color: gray;
  font-family: "Roboto", sans-serif; }

.main {
  width: 100%;
  max-width: 700px;
  margin: 5px auto 30px auto;
  padding-top: 20px; }
  .main--index {
    padding-top: 50px; }
  .main__header {
    font-family: "Cormorant SC", serif;
    text-shadow: 4px 4px 4px black;
    color: #fef9ff;
    margin-bottom: 15px;
    background: #133C55;
    border-radius: 10px;
    padding: 5px;
    border-bottom: 1.5px solid grey;
    text-align: center; }

.footer {
  color: #385572;
  font-size: 11px;
  width: 100%;
  max-width: 700px;
  margin: 0 auto 5px auto; }
  .footer__top {
    color: #1B065E;
    position: fixed;
    bottom: 7px;
    right: 10px;
    text-decoration: none; }

.footerAuthor {
  font-style: italic; }

.paragraph {
  padding: 0 5px 0 25px; }

.comeBack {
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  padding: 16px;
  background: #fefefe; }
  .comeBack__link {
    color: #1B065E; }

.about__description {
  background: linear-gradient(180deg, #59a5d8 0%, #edf2f5 100%);
  border-bottom: 1.5px solid #59A5D8;
  border-radius: 10px;
  padding: 5px 0; }

.about__header {
  color: #386FA4; }

.about__link {
  color: #1B065E; }

.about__paragraph {
  padding: 0 5px 0 25px;
  color: white; }

.article__header {
  color: #386FA4; }

.article__paragraph {
  padding: 0 0 0 5px; }

.article__section-header {
  color: #59A5D8;
  font-size: 16px; }

.article-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 10px;
  grid-template-areas: "article-list__header article-list__header" ". ." ". ." "last-article last-article"; }
  .article-list__header {
    color: #386FA4;
    grid-area: article-list__header; }
  .article-list__icon {
    width: 100%;
    max-width: 200px;
    display: block;
    margin: 0 5px 5px 0;
    float: left; }
  .article-list__link {
    color: #1B065E; }
  .article-list__paragraph {
    padding: 0 5px 0 25px; }
    .article-list__paragraph--color {
      color: #386FA4; }
  .article-list__shortcat {
    border-bottom: 1.5px solid #59A5D8;
    background: #edf2f5;
    border-radius: 10px;
    overflow: hidden;
    margin: 5px 0; }
    .article-list__shortcat--last {
      grid-area: last-article; }
  .article-list__shortcat-header {
    color: #59A5D8;
    padding: 5px 0 0 10px; }

.navigation {
  width: 100%;
  max-width: 700px;
  margin: 0 auto; }
  .navigation__item {
    border: 1px solid #d6d6d6;
    padding: 0 5px;
    background: linear-gradient(180deg, #929292 0%, #edf2f5 100%);
    font-weight: bold;
    text-align: center;
    flex-grow: 1; }
  .navigation__link {
    color: #1B065E;
    text-decoration: none; }
  .navigation__list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 30px;
    margin-bottom: 0;
    justify-content: space-around;
    align-content: center; }

.site-header {
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  background: #fefefe;
  width: 100%; }

@media (max-width: 600px) {
  .article-list {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: "article-list__header" "." "." "." "." "last-article"; }
  .navigation__list {
    flex-direction: column;
    align-content: flex-start;
    display: none; }
  .navigation__list--visible {
    display: flex; }
  .navigation__item {
    margin: 1px; }
  .navigation__switcher {
    border: none;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px; } }

@media (min-width: 601px) {
  .navigation__switcher {
    display: none; } }

@media (min-width: 1150px) {
  .main {
    max-width: 850px; }
  .navigation {
    max-width: 850px; }
  .navigation__switcher {
    display: none; } }
